import { Flex, Circle, Image, Text, Avatar, WrapItem } from "@chakra-ui/react";
import Images_Icons from "../../../constants/icons-images";
import { NotificationResponseDto } from "../interface";
import { NotificationType, useGetNotificationMutation } from "../../../redux/rtkQuery/api";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { GLOBAL_PREFIX } from "../../../constants";

interface NotificationItemProps {
  dto: NotificationResponseDto;
  lowerSec: any;
  boxCircleColor: any;
  bodyStyle: any;
  subHeadStyle: any;
}

export const NotificationItem = ({
  dto,
  lowerSec,
  boxCircleColor,
  bodyStyle,
  subHeadStyle,
}: NotificationItemProps) => {
  const [getNotification] = useGetNotificationMutation();
  let navigate = useNavigate();

  const [showHoverState, setShowHoverState] = useState<boolean>(false);
  const [updatedDto, setUpdateDto] = useState<NotificationResponseDto>(dto);

  const handleNavigation = () => {
    // if updatedDto.type === 'offer' then navigate to offer page
    if (updatedDto.type === NotificationType.OFFER) {
      // navigate(`/offer/${updatedDto.offerId}`);
      // navigate(`${GLOBAL_PREFIX}/dashboard/offers`);
      return;
    }
    // if updatedDto.type === 'brand' then navigate to brand page
    if (updatedDto.type === NotificationType.BRAND) {
      // navigate(`/brand/${updatedDto.brandId}`);
      return;
    }
    // if updatedDto.type === 'order' then navigate to order page
    if (updatedDto.type === NotificationType.ORDER && updatedDto.orderId) {
      // navigate(`${GLOBAL_PREFIX}/dashboard/orders/${updatedDto.orderId}`);
      return;
    }
    // if updatedDto.type === 'news' then navigate to news page
    if (updatedDto.type === NotificationType.NEWS && updatedDto.news_id) {
      // navigate(`/news/${updatedDto.news_id}`);
      return;
    }
    // if updatedDto.type === 'user' then navigate to user page
    if (updatedDto.type === NotificationType.OTHER && updatedDto.userId) {
      // navigate(`/user/${updatedDto.userId}`);
      return;
    }
  };

  async function handleGetAndUpdateNotification() {
    if (updatedDto.isRead) {
      setShowHoverState(true);
      handleNavigation();
      return;
    }
    await getNotification({ id: dto.id })
      .then((res: any) => {
        if (res?.data?.statusCode === 200) {
          setShowHoverState(true);
          setUpdateDto(res?.data.data);
          handleNavigation();
        }
      })
      .catch((e: any | unknown) => {
        console.log("GET: Handle get and update Notification", e);
      });
  }

  return (
    <Flex
      {...lowerSec}
      flexDir={"row"}
      justify={"space-between"}
      padding="1rem"
      borderBottom={"1px"}
      borderBottomColor="gray.300"
      bg={showHoverState ? "#F2F2F2" : ""}
      onClick={handleGetAndUpdateNotification}
    >
      {/* status notification  */}
      <Flex alignItems={"center"}>
        {!updatedDto.isRead && <Circle size="10px" {...boxCircleColor} />}

        {/* The brand image is here */}
        <WrapItem>
          <Avatar m={4} size="sm" name="Brand Name" src={updatedDto?.icon} />{" "}
        </WrapItem>
      </Flex>

      <Flex flexDir={"column"} mr="4%">
        <Text {...bodyStyle}>{updatedDto.title}</Text>
        <Text {...subHeadStyle}>{updatedDto.message}</Text>
      </Flex>
      {/* The collection brand image is here */}
      <Image
        h="100px"
        w={"100px"}
        src={updatedDto?.image}
        objectFit={"contain"}
        bg="grey"
        fallbackStrategy="beforeLoadOrError"
        fallbackSrc={Images_Icons.appLogoPlaceholder}
      />
    </Flex>
  );
};
