import { Flex, TextProps, Text } from "@chakra-ui/react"
import { CustomCheckbox } from "./CustomCheckbox"
import { NotificationType } from "../../../redux/rtkQuery/api";
import { Dispatch, SetStateAction } from "react";

interface FilterListItemProps {
    isItemEven: boolean,
    type: NotificationType
    itemHeading: string;
    itemHeadingProps?: TextProps;
    itemSubHeading: string;
    itemSubHeadingProps?: TextProps;
    selectedFilter: NotificationType[] | undefined;
    setFilter: Dispatch<SetStateAction<NotificationType[] | undefined>>
}

export const FilterListItem = ({
    itemHeading,
    itemSubHeading,
    itemHeadingProps,
    itemSubHeadingProps,
    isItemEven,
    type,
    selectedFilter,
    setFilter,
}: FilterListItemProps) => {
    const bg = isItemEven ? '#F2F2F2' : '';
    const borderColor = !isItemEven ? "1px solid rgba(0, 0, 0, 0.07)" : '';

    // update the filter state
    const handleFilter = (type: NotificationType) => {
        // if selectedFilter is undefined then set it to an array with the type
        if (!selectedFilter) {
            setFilter([type]);
            return;
        }

        // if selectedFilter is not undefined then check if the type is already in the array
        // if it is then remove it from the array
        // if it is not then add it to the array
        if (selectedFilter?.find((value) => value === type)) {
            setFilter(selectedFilter?.filter((value) => value !== type));
            return;
        }

        setFilter([...selectedFilter, type]);
    }

    return (
        <Flex
            flexDir={"row"}
            justify={"space-between"}
            alignItems="center"
            padding="1rem"
            bg={bg}
            borderColor={borderColor}
        >
            <Flex flexDir="column">
                <Text {...itemHeadingProps}>{itemHeading}</Text>
                <Text {...itemSubHeadingProps}>{itemSubHeading}</Text>
            </Flex>

            <Flex>
                <CustomCheckbox value={type} isChecked={selectedFilter?.find((value) => value === type) ? true : false} onChange={(e) => handleFilter(e.target.value as NotificationType)} />
            </Flex>
        </Flex>
    )
}